<template>
  <el-cascader
    :model-value="areaCode"
    :options="areaList"
    @change="handleChange"
  />
</template>
<script setup lang="ts">
import { computed } from "vue";
import areaList from "./areaList.js";

const props = withDefaults(
  defineProps<{
    index?: number; // 组件作为列表子项时的索引
    area?: string[]; // 地区code
  }>(),
  {
    area: () => [],
    index: 0,
  }
);
const areaCode = computed(() => {
  return getLabelByValue(props.area).map((item) => item.value);
});
const emits = defineEmits<{
  (e: "emitFunc", value: Record<string, any>): void;
}>();
const handleChange = (areaCode: string[]) => {
  emits("emitFunc", {
    areaCode,
    areaLabel: getLabelByValue(areaCode).map((item) => item.label),
    area: getLabelByValue(areaCode),
    index: props.index,
  });
};
const getLabelByValue = (value: string[]) => {
  if (!value || !value.length) return [];
  const list: any[] = [];
  const startCode = value[0].slice(0, 2); // 获取省份code前两位。
  const provinceData = areaList.find((item) =>
    item.value.startsWith(startCode)
  )!;
  list.push({ label: provinceData.label, value: provinceData.value });
  if (!provinceData.children || !provinceData.children.length) return list;
  const cityData = provinceData.children.find(
    (item) => item.value === value.at(-1)
  )!;
  list.push({ label: cityData.label, value: cityData.value });
  return list;
};
</script>
