<template>
  <div class="product-detail">
    <BaseServiceInfo :serviceData="serviceInfo"></BaseServiceInfo>
    <div class="service-specs">
      <el-form label-width="160px">
        <template v-if="purchasedData.length">
          <el-form-item
            v-for="(item, index) in purchasedData"
            :key="index"
            label="已购详情"
          >
            <div class="purchased-info">
              <span>拨打方式：{{ item.groupNameList[0] }}</span>
              <span>线路类型：{{ item.groupNameList[1] }}</span>
              <span>号码月租：{{ item.groupNameList[2] }}</span>
              <span>月租周期：{{ item.serviceMonthCycle }}个月</span>
              <span>已购号码数：{{ item.owned }}个</span>
              <span>服务到期时间：{{ item.endTime }}</span>
            </div>
          </el-form-item>
        </template>
        <el-form-item label="业务场景">
          <el-input
            readonly
            v-model="scenarios.label"
            @click="openScenarios"
          ></el-input>
        </el-form-item>
        <el-form-item label="拨打方式">
          <CustomRadioButtonGroup
            :list="callTypeList"
            :model-value="formData.callType"
            minWidth="95px"
            @update:modelValue="specsCharge('callType', $event)"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="适用业务场景" v-if="lineTag.label.length">
          <CustomCheckboxButtonGroup
            :list="lineTag.list"
            :model-value="lineTag.label"
            direction="row"
          ></CustomCheckboxButtonGroup>
        </el-form-item>
        <el-form-item label="线路类型">
          <CustomRadioButtonGroup
            :list="lineTypeList"
            :model-value="formData.lineType"
            minWidth="95px"
            @update:modelValue="specsCharge('lineType', $event)"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="号码类型" v-if="numberTypeList.length">
          <CustomRadioButtonGroup
            minWidth="95px"
            :tooltip="{
              1: '该号码的归属地为您选购的指定地区',
              2: '该号码为混显号码，外显号与归属地为系统随机分配',
            }"
            :list="numberTypeList"
            :model-value="formData.numberType"
            @update:modelValue="specsCharge('numberType', $event)"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="号码月租" v-if="specInfo">
          <p class="word">
            {{ agentInfo.unitPrice }}元/月
            <el-tooltip
              v-if="messageData.tip"
              placement="top-start"
              trigger="hover"
              popper-class="custom-tooltip"
              :content="messageData.tip"
            >
              <img
                class="question-icon"
                src="@/assets/images/icon-question.png"
                alt=""
              />
            </el-tooltip>
          </p>
        </el-form-item>
        <el-form-item label="合作周期">
          <template v-if="!formData.isExistResources">
            <el-select
              v-model="formData.cycleCount"
              @change="getPrice"
              :disabled="!scenarios.label.length"
            >
              <el-option
                v-for="item in cycleList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-input-number
              v-if="formData.cycleCount === -1"
              v-model="formData.customCycle"
              @change="getPrice"
              :min="1"
              :max="180"
              :step="1"
              :value-on-clear="1"
            />
          </template>
          <template v-else>
            <el-input :value="surplusCycle + '个月'" readonly />
          </template>
        </el-form-item>
        <template v-if="scenarios.label.length">
          <el-form-item v-if="isVirtual" label="号码归属地">
            <TransitionGroup name="list" tag="ul">
              <li
                class="belonging-item"
                v-for="(item, index) in formData.belongingList"
                :key="item.id"
              >
                <BelongingPlace :index="index" @emitFunc="chinaAreaChange" />
                <div class="number-count">
                  <span class="word">订购数</span>
                  <el-select v-model="item.count" @change="getPrice">
                    <el-option
                      v-for="sonItem in numberList"
                      :label="sonItem.label"
                      :value="sonItem.value"
                      :key="sonItem.value"
                    ></el-option>
                  </el-select>
                  <el-input-number
                    v-if="item.count === -1"
                    v-model="item.customCount"
                    :min="1"
                    :precision="0"
                    @change="getPrice"
                  />
                </div>
                <div class="operate">
                  <span @click="handlePlus(index)" class="increase"></span>
                  <span
                    @click="handleRemove(index)"
                    v-if="index !== 0"
                    class="decrease"
                  ></span>
                </div>
              </li>
            </TransitionGroup>
          </el-form-item>
          <el-form-item v-else label="订购数">
            <el-input v-model="numberCount" readonly></el-input>
          </el-form-item>
        </template>
        <el-form-item class="message-form-item">
          <p class="word">{{ sumDescribe }}</p>
          <p
            class="word"
            v-for="(item, index) in messageData.list"
            :key="index"
          >
            {{ index + 1 }}、{{ item }}
          </p>
        </el-form-item>
      </el-form>
      <BaseSumPrice
        @emitFunc="handleBuy"
        :buyPrice="agentInfo.sumPrice"
        :canBuy="canBuy"
        :hasAgentPrice="agentInfo.hasAgentPrice"
      ></BaseSumPrice>
    </div>
  </div>
  <BusinessScenariosDialog
    ref="scenariosRef"
    @emitFunc="updateScenarios"
  ></BusinessScenariosDialog>
</template>

<script setup lang="ts">
import { computed, reactive, ref, shallowRef, watch } from "vue";
import { ElMessage } from "element-plus";

import { LocalStorage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";
import {
  createdOrder,
  handlePrice,
} from "@components/order-management/create-order/product-detail/common";

import { companyLineTypeApi } from "@api/business-management-api";
import {
  canBuyNumberCountApi,
  checkResourceApi,
} from "@api/order-management-api";

import BaseServiceInfo from "./base-service-info.vue";
import BelongingPlace from "@/components/common/belonging-place/index.vue";
import CustomRadioButtonGroup from "@components/common/custom-radio-button-group.vue";
import BusinessScenariosDialog from "@/components/common/business-scenarios-dialog.vue";
import CustomCheckboxButtonGroup from "@components/common/custom-checkbox-button-group.vue";
import BaseSumPrice from "@components/order-management/create-order/product-detail/base-sum-price.vue";

// 周期列表
const cycleList = [
  { value: 3, label: "3个月" },
  { value: 6, label: "6个月" },
  { value: 12, label: "12个月" },
  { value: -1, label: "自定义" },
];
// 号码数量列表
const numberList = [
  { value: 5, label: "5个号码" },
  { value: 10, label: "10个号码" },
  { value: 15, label: "15个号码" },
  { value: -1, label: "自定义" },
];
const scenariosRef = ref();
const { routeQuery } = useVueRouter();
const { bd_userInfo: userInfo } = LocalStorage.batchGetItem(["bd_userInfo"]);

const props = withDefaults(
  defineProps<{
    tagList: Record<string, any>[];
    productData: Record<string, any>;
    purchasedData: Record<string, any>[];
  }>(),
  {
    tagList: () => [],
    purchasedData: () => [],
  }
);

// 线路列表
const lineList = ref([]);
// 业务场景
const scenarios = reactive({
  list: props.tagList,
  label: props.tagList.map((item) => item.tagName),
});

const formData = reactive({
  lineType: "", // 线路
  callType: "", // 拨打
  numberType: "", // 号码
  cycleCount: 3, // 月租周期
  customCycle: 3, // 自定义月租周期
  // 号码归属地
  belongingList: [
    {
      id: `${Date.now()}`,
      value: "", // 选择的号码归属地code
      label: "", // 选择的号码归属地name
      count: 5, // 号码数量
      customCount: 1, // 号码数量
    },
  ],
  canBuyCount: 0, // 中间号：订购数
  isExistResources: false, // 是否存在资源
});

// 购买的商品价格信息
const agentInfo = shallowRef({
  price: "0.00",
  sumPrice: "0.00",
  unitPrice: "0.00",
  addedPrice: "0.00",
  hasAgentPrice: false,
  subMarketList: [],
});

// 所有信息
const productInfo: Record<string, any> = computed(() => {
  getLineList();
  getCanBuyNumberCount();
  return props.productData;
});
// 服务基本信息
const serviceInfo: Record<string, any> = computed(() => {
  return productInfo.value.serviceInfo;
});
// 合并增值服务，组合新的增值服务列表
const addedSpecsList: Record<string, any> = computed(() => {
  // 接口客户不需要回拨线路
  const addedServiceArr = productInfo.value.addedServiceList ?? [];
  const addedServiceList =
    routeQuery.value.businessType === "1"
      ? addedServiceArr.filter(
          (item) => item.addedServiceCode !== "CALL_NUMBER_HB_MID"
        )
      : addedServiceArr;
  const list = addedServiceList.reduce((pre, item) => {
    return pre.concat(item.specsList);
  }, []);
  return list.map((item) => {
    return {
      ...item,
      groupCodeList: item.groupCodes.split(","),
      groupNameList: item.groupNames.split(","),
      groupValueList: item.groupValues.split(","),
    };
  });
});
const callTypeList = computed(() => {
  const valueList = lineList.value.map((item: any) => `${item.lineType}`);
  const specsList = addedSpecsList.value.filter((item: any) => {
    if (!valueList.length) {
      return true;
    } else {
      return valueList.includes(item.groupValueList[0]);
    }
  });
  const list = specsList.reduce((acc: any[], item: any) => {
    const findItem = acc.find(
      (sonItem: any) => sonItem.value === item.groupValues[0]
    );
    if (!findItem) {
      const skuIem = {
        label: item.groupNameList[0],
        value: item.groupValueList[0],
        disabled: !scenarios.list.length,
      };
      return [...acc, skuIem];
    } else {
      return acc;
    }
  }, []);
  if (scenarios.list.length) {
    formData.callType = list[0].value;
  } else {
    formData.callType = "";
  }
  return list;
});
const lineTypeList = computed(() => {
  const valueList = [formData.callType || "2"].filter((item) => !!item);
  const specsList = addedSpecsList.value.filter((item: any) => {
    return item.groupValues.startsWith(valueList.toString());
  });
  const list = specsList.reduce((acc: any[], item: any) => {
    const findItem = acc.find(
      (sonItem: Record<string, any>) => sonItem.value === item.groupValueList[1]
    );
    if (!findItem) {
      const skuIem = {
        label: item.groupNameList[1],
        value: item.groupValueList[1],
        disabled: !formData.callType,
      };
      return [...acc, skuIem];
    } else {
      return acc;
    }
  }, []);
  if (formData.callType && list.length) {
    formData.lineType = list[0].value;
  } else {
    formData.lineType = "";
  }
  return list;
});
const numberTypeList = computed(() => {
  const valueList = [formData.callType || "2", formData.lineType || "4"].filter(
    (item) => !!item
  );
  const specsList =
    formData.callType === "2"
      ? addedSpecsList.value.filter((item: any) => {
          return item.groupValues.startsWith(valueList.toString());
        })
      : [];
  const lineInfo = lineList.value.find(
    (item: any) => `${item.lineType}` === formData.callType
  );
  const list = specsList.reduce((acc: any[], item: any) => {
    const { groupNameList = [], groupValueList = [] } = item;
    const findItem = acc.find(
      (sonItem: Record<string, any>) => sonItem.value === groupValueList[2]
    );
    if (!findItem) {
      const skuIem = {
        label: groupNameList[2],
        value: groupValueList[2],
      };
      if (groupValueList[2] === "1") {
        Reflect.set(skuIem, "disabled", !formData.lineType);
      } else {
        Reflect.set(
          skuIem,
          "disabled",
          !formData.lineType || !lineInfo?.isSupportMixedNumber!
        );
      }
      return [...acc, skuIem];
    } else {
      return acc;
    }
  }, []);
  if (formData.lineType && list.length) {
    formData.numberType = list[0].value;
  } else {
    formData.numberType = "";
  }
  return list;
});
const lineTag = computed(() => {
  const info = lineList.value.find((item: any) => {
    return item.lineType === Number(formData.callType);
  }) ?? { lineLabelName: "", isSupportMixedNumber: true };
  const lineTagList = info.lineLabelName ? info.lineLabelName.split(",") : [];
  return {
    list: lineTagList.map((item) => {
      return {
        label: item,
        value: item,
      };
    }),
    label: lineTagList,
    isMixedNumber: info.isSupportMixedNumber,
  };
});
// 当前选择的规格信息
const specInfo = computed(() => {
  const valueList = [
    formData.callType,
    formData.lineType,
    formData.numberType,
  ].filter((item) => !!item);
  return valueList.length
    ? addedSpecsList.value.find((item: any) => {
        return item.groupValues.startsWith(valueList.toString());
      })
    : {};
});
// 选择的线路类型是否是虚拟号线路
const isVirtual = computed(() => {
  return (
    ["2", "3"].includes(formData.callType) || ["7"].includes(formData.lineType)
  );
});
// 获取对应的信息提示
const messageData = computed(() => {
  const list = [
    "购买当日开始收取当月月租，号码发放完成不可退款；可在号码管理页面退订号码，退订号码不退月租费。",
    "当月退订的号码不收取次月月租费，不使用的号码需要在结算日前退订，以免持续扣费。",
    "账号欠费超过保留期后会自动退订号码资源，为避免欠费影响业务使用，请及时充值。",
  ];
  const { serviceCode = "" } = specInfo.value;
  switch (serviceCode) {
    case "CALL_NUMBER_VIR_SMALL":
      return {
        serviceCode: "CALL_NUMBER_VIR_SMALL",
        tip: "1个号码可以绑定1个坐席；",
        list: [
          "小号线路坐席（A路）拨打被叫（B路），由数企提供的中间号直接呼叫B，被叫手机端显示为中间号来电。",
          ...list,
        ],
      };
    case "CALL_NUMBER_VIR_LANDLINE":
      return {
        serviceCode: "CALL_NUMBER_VIR_LANDLINE",
        tip: "1个号码可以绑定1个坐席；",
        list: [
          "小号线路坐席（A路）拨打被叫（B路），由数企提供的中间号直接呼叫B，被叫手机端显示为中间号来电。",
          ...list,
        ],
      };
    case "CALL_NUMBER_HB_MID":
      return {
        serviceCode: "CALL_NUMBER_HB_MID",
        tip: "1个号码可以绑定3个坐席；",
        list: [
          "回拨线路坐席（A路）拨打被叫（B路），由数企先呼叫A，等A接通后，再呼叫B，B接通后，AB双方建立通话。坐席手机端显示为中间号。被叫手机",
          ...list,
        ],
      };
    default:
      return {
        serviceCode: "",
        tip: "",
        list: list,
      };
  }
});

// 号码总数
const numberCount = computed(() => {
  const totalCount = formData.belongingList.reduce((pre, item) => {
    const count = item.count === -1 ? item.customCount : item.count;
    return pre + count;
  }, 0);
  if (scenarios.label.length) {
    return isVirtual.value ? totalCount : formData.canBuyCount;
  } else {
    return 0;
  }
});

// 剩余月租周期（升级的时候需要）
const surplusCycle = computed(() => {
  const findItem = props.purchasedData.find(
    (item) => item.resourceCode == specInfo.value?.serviceCode
  );
  if (formData.isExistResources && findItem) {
    const endTime = findItem.endTime;
    const endYear = dayjs(endTime).year();
    const endMonth = dayjs(endTime).month();
    const endDate = dayjs(endTime).date();
    const curYear = dayjs().year();
    const curMonth = dayjs().month();
    const curDate = dayjs().date();
    return (
      (endYear - curYear) * 12 +
      (endMonth - curMonth) +
      (endDate - curDate > 0 ? 1 : 0)
    );
  } else {
    return 0;
  }
});

// 周期
const cycleCount = computed(() => {
  const count_1 =
    formData.cycleCount === -1 ? formData.customCycle : formData.cycleCount;
  const count_2 = surplusCycle.value;
  return formData.isExistResources ? count_2 : count_1;
});

// 总计描述
const sumDescribe = computed(() => {
  return `合计：${agentInfo.value.unitPrice}元/月 x 周期${cycleCount.value}个月 x 订购数${numberCount.value}个号码 = ${agentInfo.value.sumPrice}元`;
});

// 能否购买
const canBuy = computed(() => {
  return !(formData.isExistResources && formData.lineType === "1");
});

// 打开业务场景弹框
async function openScenarios() {
  const list = scenarios.list.map((item: any) => {
    return {
      id: item.id,
      pid: item.pid,
      name: item.tagName || item.name,
    };
  });
  scenariosRef.value.trigger(true, list);
}

// 更新业务标签
async function updateScenarios({
  list = [],
  label = [],
}: {
  list: any[];
  label: any[];
}) {
  scenarios.list = list;
  scenarios.label = label;
  await getLineList();
}

// 根据业务场景获取线路
async function getLineList() {
  const { data } = await companyLineTypeApi.getData(
    scenarios.list.map((item) => item.id).toString()
  );
  lineList.value = companyLineTypeApi.handleData(data);
}

// 拨打方式、线路类型change事件
function specsCharge(
  type: "callType" | "lineType" | "numberType",
  value: string
) {
  formData[type] = value;
}

// 根据号码类型获取当前可购买号码数量
async function getCanBuyNumberCount() {
  const { data } = await canBuyNumberCountApi.getData({
    companyId: routeQuery.value.companyId,
    numberType: "1",
    seatNumber: "",
  });
  formData.canBuyCount = data;
}

// 根据号码类型获取当前可购买号码数量
async function checkResource() {
  const { data } = await checkResourceApi.getData({
    companyId: routeQuery.value.companyId,
    resourceCode: specInfo.value?.serviceCode,
  });
  formData.isExistResources = data;
}

// 获取省市区
async function chinaAreaChange(value: Record<string, any>) {
  const { index, areaLabel, areaCode } = value;
  formData.belongingList[index].label = areaLabel
    .toString()
    .toString()
    .replace(/,/g, "-");
  formData.belongingList[index].value = areaCode.toString();
}

// 添加号码归属地
const handlePlus = (index: number) => {
  if (formData.belongingList.length >= 5) {
    return ElMessage.error("最多可选择5个");
  }
  formData.belongingList.splice(index + 1, 0, {
    id: `${Date.now()}`,
    value: "", // 选择的号码归属地
    label: "", // 选择的号码归属地
    count: 5, // 号码数量
    customCount: 1, // 号码数量
  });
  getPrice();
};

// 减去号码归属地
const handleRemove = (index: number) => {
  formData.belongingList.splice(index, 1);
  getPrice();
};

// 获取价格参数
function handleParams() {
  const tagList = scenarios.list.map((item) => {
    return {
      id: item.id,
      name: item.name,
    };
  });
  const belongingDesc = formData.belongingList.reduce((pre, cur) => {
    const count = cur.count === -1 ? cur.customCount : cur.count;
    return pre + `号码归属地：${cur.label}x${count}个；`;
  }, "");
  const belongingList = formData.belongingList.map((item) => {
    const count = item.count === -1 ? item.customCount : item.count;
    return {
      districtCode: item.value,
      districtName: item.label,
      number: count,
    };
  });
  const {
    coding = "",
    serviceCode = "",
    groupCodeList = [],
    groupNameList = [],
    groupValueList = [],
  } = specInfo.value;
  const params = {
    helpOrder: 1,
    agentId: userInfo.profileId,
    companyId: routeQuery.value.companyId, // 代理商子公司id
    profileId: routeQuery.value.profileId, // 代理商子公司操作人id
    buyType: formData.isExistResources ? "UPGRADE" : "BUY_SERVER", // 购买：BUY_SERVER,升级：UPGRADE
    businessTagsInfo: JSON.stringify(tagList),
    shopBuyList: [
      {
        agentId: userInfo.profileId,
        dateType: "2", // 时间单位 1:日, 2:月, 3:年
        dateValue: cycleCount.value, // 时长
        marketCode: routeQuery.value.serviceCode,
        userNumber: numberCount.value,
        paramStr: isVirtual.value ? JSON.stringify(belongingList) : "",
        coding: [
          {
            code: serviceCode,
            coding: coding,
            num: numberCount.value,
          },
        ],
        baseCodeList: groupCodeList.map((item, index) => {
          return {
            baseCode: item,
            number: groupValueList[index],
          };
        }),
      },
    ],
    sumPrice: agentInfo.value.sumPrice,
  };
  const commodityInfo = {
    list: [
      {
        ...serviceInfo.value,
        price: agentInfo.value.price,
        specsInfo: `拨打方式：${groupNameList[0]}；线路类型：${
          groupNameList[1]
        }；号码类型：${groupNameList[2]}；号码月租：${
          agentInfo.value.unitPrice
        }元/月；月租周期：${cycleCount.value}个月；号码数量：${
          numberCount.value
        }个；${isVirtual.value ? belongingDesc : ""}`,
        augmentedServiceList: agentInfo.value?.subMarketList.map(
          (item: Record<string, any>) => {
            if (item.calculateType === "1") {
              item.specsInfo = `${cycleCount.value}个月`;
            }
            return item;
          }
        ),
      },
    ],
    sumPrice: agentInfo.value.sumPrice,
  };
  return {
    params,
    commodityInfo,
  };
}

// 计算价格
async function getPrice() {
  const params = handleParams().params;
  agentInfo.value = await handlePrice(params);
}

// 购买
function handleBuy() {
  if (!scenarios.list || !scenarios.list.length) {
    ElMessage.warning("请选择业务场景!");
    return;
  }
  // 不是虚拟线路，直接终止
  if (!isVirtual.value) {
    createdOrder(handleParams());
    return;
  }
  const list = formData.belongingList.filter(
    (item) => !item.label || !item.value
  );
  const value = new Set(formData.belongingList.map((item) => item.value));
  if (list.length) {
    ElMessage.error("请选择号码归属地");
    return;
  }
  if (value.size !== formData.belongingList.length) {
    ElMessage.error("号码归属地不能重复");
    return;
  }
  createdOrder(handleParams());
}

watch(
  () => specInfo.value,
  async (cur) => {
    if (cur && Object.keys(cur).length) {
      if (cur.serviceCode) {
        await checkResource();
        await getPrice();
      }
    }
  },
  {
    deep: true,
  }
);
</script>
